<template>
  <!-- Modals -->
  <div class="row">
    <modal :show="modalStatus">
      <h6 class="modal-title" id="modal-title-default">Delete Admin</h6>

      <p class="mt-4">Be aware when deleting the admin, he will be removed from the system and can't be returned on</p>
      <div class="d-flex flex-column gap-4"></div>

      <div class="d-flex justify-content-between gap-1">
        <button-component
          class="mt-4"
          variant="gradient"
          color="danger"
          size="sm"
          :isLoading="actionInProgress.loading"
          @click="deleteAdmin()"
        >
          Delete
        </button-component>
        <button-component class="mt-4" variant="gradient" color="primary" size="sm" @click="toggleModal()">
          Close
        </button-component>
      </div>
    </modal>
  </div>
</template>
<script>
  import Modal from '@/components/Modal-dialog';
  import ButtonComponent from '@/components/Button.vue';
  export default {
    name: 'delete-admin',
    components: { Modal, ButtonComponent },

    computed: {
      modalStatus() {
        return this.$store.state.AdminsStore.modals.deleteAdmin;
      },
      adminId() {
        return this.$store.state.AdminsStore.adminId;
      },
      actionInProgress() {
        return this.$store.state.AdminsStore.progress.deleteAdmin;
      },
    },

    methods: {
      toggleModal() {
        this.$store.commit('AdminsStore/TOGGLE_DELETE_ADMIN_MODAL');
      },

      deleteAdmin() {
        this.$store.dispatch('AdminsStore/deleteAdmin', this.adminId);
      },
    },
  };
</script>
<style></style>
